import React from 'react'
import testimonials from "../static/TestimonialDescriptions.js"

const Testimonials = () => {
    return(
      <div class='card'>
        <h2>Testimonials</h2>
        {testimonials.map(testimonial => (
          <div>
            <p style={{fontWeight: "bold", textAlign: "left", marginBottom: 0}}>{testimonial.name} - {testimonial.role}</p>
            <p style={{marginTop: "5px", fontStyle: "italic", fontSize: "1.25vw"}}>{testimonial.relationship}</p>   
            <blockquote style={{textAlign: "left", fontSize: "1.25vw"}}>
            "{testimonial.text}"
            </blockquote>
            <hr/>
          </div>
        ))}
      </div>
    )
  }

export default Testimonials;